<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread面包屑带有x结构的-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <!-- 分类的面包屑 -->
            <li class="with-x" v-if="searchParams.categoryName">
              {{searchParams.categoryName}}
              <i @click="removecategoryName">×</i>
            </li>
            <!-- 关键字面包屑 -->
            <li class="with-x" v-if="searchParams.keyword">
              {{searchParams.keyword}}
              <i @click="removekeyword">×</i>
            </li>
            <!-- 品牌的面包屑 -->
            <li class="with-x" v-if="searchParams.trademark">
              {{searchParams.trademark.split(':',)[1]}}
              <i @click="removetrademark">×</i>
            </li>
            <!-- 平台的售卖属性值的展示面包屑 -->
            <li class="with-x" v-for="(attrValue,index) in searchParams.props" :key="index">
              {{attrValue.split(':')[1]}}
              <i @click="removeprops(index)">×</i>
            </li>
          </ul>
        </div>

        <!--selector-->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo='attrInfo' />

        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
               <!-- 排序的结构 -->
              <ul class="sui-nav">
                <li :class="{active:isOne}" @click="changeOrder('1')">
                  <a >综合 <i v-show="isOne" class="iconfont" :class="{'icon-jiantoushang':isAsc,'icon-jiantouxia':isDesc }" ></i> </a>
                </li>
                <li :class="{active:isTwo}" @click="changeOrder('2')">
                  <a >价格 <i v-show="isTwo" class="iconfont" :class="{'icon-jiantoushang':isAsc,'icon-jiantouxia':isDesc }" ></i></a>
                </li>
               
              </ul>
            </div>
          </div>
          <!-- 销售产品列表 -->
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="good in goodsList" :key="good.id">
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link :to='`/detail/${good.id}`'>
                      <img :src="good.defaultImg"  />
                    </router-link>                            
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥</em>
                      <i>&nbsp; {{good.price}}</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a
                      target="_blank"
                     
                      title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】"
                    >{{good.title}}</a>
                  </div>
                  <div class="commit">
                    <i class="command">
                      已有
                      <span>2000</span>人评价
                    </i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      target="_blank"
                      class="sui-btn btn-bordered btn-danger"
                    >加入购物车</a>
                    <a href="javascript:void(0);" class="sui-btn btn-bordered">收藏</a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
              <!-- 分页器 -->
              <Pagination 
                  :pageNo='searchParams.pageNo' 
                  :pageSize='searchParams.pageSize'
                  :total=' total' :continuse='5'
                  @getPageNo='getPageNo'>
              </Pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters,mapState } from "vuex";
import SearchSelector from "./SearchSelector/SearchSelector";
export default {
  name: "Search",
  data() {
    return {
      // 带给服务器的参数
      searchParams: {
        // 一级分类的id
        category1Id: "",
        //  二级分类的id
        category2Id: "",
        //  三级分类的id
        category3Id: "",
        //  用户选中分类的名字
        categoryName: "",
        //  关键字，用户输入的
        keyword: "",
        // 排序,初识状态应该是综合 且降序
        order: "1:desc",
        // 当前分页器是当前第几页
        pageNo: 1,
        // 每一页有多少条数据
        pageSize: 3,
        // 平台售卖属性操作带来的参数
        props: [],
        // 品牌
        trademark: ""
      }
    };
  },
  components: {
    SearchSelector
  },
  // computed:{
  //    ...mapState({
  //      goodsList:state=>state.search.searchList.goodsList

  // })
  // }
  // 由于方法路径过长，所以使用Getters的方法
  //  计算属性
  // 可以把我们将来组件中需要用的数据简化一下，将来组件在获取数据的时候就方便了
  computed: {
    // 由于仓库中的state是分模块的所以使用对象的方式，而getters不是这样，它没有划分模块，可以直接写成数组

    ...mapGetters(["goodsList"]),
    // 获取search展示的产品有多少数据
    ...mapState({
      total:state=>state.search.searchList.total
    }),
    isOne(){
      return this.searchParams.order.includes(1)
    },
    isTwo(){
      return this.searchParams.order.includes(2)
    },
    isAsc(){
      return this.searchParams.order.includes('asc')
    },
     isDesc(){
      return this.searchParams.order.includes('desc')
    },
  
  
    
  },
  // 当组件挂载完毕之前执行一次【先于mounted之前】
  beforeMount() {
    // console.log('=====',this.$route.query)
    // console.log('=====',this.$route.params)
    // 复杂的写法
    // this.searchParams.category1Id=this.$route.query.category1Id
    //  this.searchParams.category2Id=this.$route.query.category2Id
    //   this.searchParams.category3Id=this.$route.query.category3Id
    //   this.searchParams.categoryName=this.$route.query.categoryName
    //   this.searchParams.keyword=this.$route.params.keyword
    // ---------------------------
    // Object.assign   ES6新增的语法,合并对象,简单写法
    // 在发送请求之前把接口需要传递参数，进行治理（在给服务器发请求之前，吧参数整理好，服务器就会返回查询的数据）
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
  },

  methods: {
    // 收集平台属性的回调（自定义事件）
    attrInfo(attrs,attrValue){
        // console.log("父组件已经拿到数据了",attrs,attrValue)
        let props=`${attrs.attrId}:${attrValue}:${attrs.attrName}`
        
        // 数组去重1.
      //   let c =new Set(this.searchParams.props);
      //  this.searchParams.props= Array.from(c)
      //    this.getData();
        // 数组去重3.
        if(this.searchParams.props.indexOf(props)==-1)this.searchParams.props.push(props)
         this.getData();
      },

    // 自定义事件回调
    trademarkInfo(trademark) {
      // 整理品牌字段的参数 "ID:品牌名称"
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
      // 整理参数后再次发送请求获取search模块列表的数据进行展示
      this.getData();
    },
    //在发请求之前带给服务器参数searchParams参数发生变化 有数值带给服务器
    // 向服务器发送请求获取search模块数据(根据参数不同返回不同的数据进行展示)
    //  把这次请求封装成一个函数，当你需要在调用的时候调用即可
    getData() {
      this.$store.dispatch("getSearchList", this.searchParams);
    },
    //删除分类的名字
    removecategoryName() {
      // 带给服务器参数说明可有可无的：如果属性值为空的字符串还是会把相应 的字段带给服务器
      // 但是你把相应的字段变为undefined,当前的这个字段不会带给服务器
      this.searchParams.categoryName = undefined;
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      // 把带给服务器的参数置空的，还需要向服务器发请求
      this.getData();
      // 地址栏也需要改:进行路由跳转
      if (this.$route.params) {
        this.$router.push({ name: "search", params: this.$route.params });
      }
    },
    // 删除关键字
    removekeyword() {
      // 给服务器带的参数search的keyword置空
      this.searchParams.keyword = undefined;
      // 再次发请求
      this.getData();
      // 通知兄弟组件Header清除关键字
      this.$bus.$emit("clear");
      this.$router.push({
        name: "search",
        query: this.$route.query
      });
    },
    // 删除点击的标签
    removetrademark(){
       // 给服务器带的参数search的trademark品牌信息置空
      this.searchParams.trademark = undefined;
       // 再次发请求
      this.getData();
    },
    //删除售卖的属性
    removeprops(index){
      // 再次整理参数
      this.searchParams.props.splice(index,1)
       // 再次发请求
      this.getData();
      
    },
    // 排序的操作flag是一个标记代表用户点击的是综合1还是价格2
    changeOrder(flag){
      let originOrder=this.searchParams.order
      // 这里获取到的是最开始的状态
      let orignFlag=this.searchParams.order.split(':')[0]
      let orginSort=this.searchParams.order.split(':')[1]
      let newOrder=''
      
      if(originOrder.includes(flag)){
        // 如果点的不变，那就切换升降序
       newOrder=`${orignFlag}:${orginSort=='desc'?"asc":'desc'}`
      }else{
        console.log(flag,"----------")
        
        newOrder=`${flag}:'desc'`
      }
     this.searchParams.order= newOrder
     this.getData()
    },
    // 自定义事件的回调函数，获取当前为第几页
    getPageNo(PageNo){
      // console.log('父组件已经收到了数据',PageNo)
      // 整理带给服务器参数
       this.searchParams.pageNo=PageNo
      //  再次发请求
       this.getData()
    }
  },

  watch: {
    $route(newvalue, oldvalue) {
      // 每一次请求完毕应该把响应的1,2,3级id置空，让他接受下一次的相应1,2,3id
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;

      // 监听路由的信息是否发生变化再次发送请求
      // 再次发送请求之前整理带给服务器参数
      Object.assign(this.searchParams, this.$route.query, this.$route.params);

      this.getData();
    }
  },
  mounted() {
    this.getData();
  }
};
</script>

