// 对axios进行二次封装
import axios from 'axios'
// 引入进度条
import nprogress from 'nprogress'
// start:进度条开始 done进度条结束
// 引入进度条的样式
import 'nprogress/nprogress.css'

// 利用axios对象的方法create,去创建一二个axios实例
// requert就是axios,只不过稍微配置一下
const requests=axios.create({
    // 基础路径，发送请求的时候，路径当中会出现/api
    baseURL: '/api',
    // 代表请求超时的时间
    timeout:5000,
    
})
// 请求拦截器，在发请求之前，请求拦截器可以监测到，可以请求发出去之前做一些事情
requests.interceptors.request.use((config)=>{
    // config配置对象，对象里面有一个属性很重要，请求头
    // 进度条开始动
    nprogress.start()
    return config
})
// 响应拦截器
requests.interceptors.response.use((res)=>{
    // 进度条结束
    nprogress.done()
     // 成功的回调函数，服务器响应数据回来以后，响应拦截器可以监测到，可以做一些事情
     return res.data
},(err)=>{
    // 服务器响应失败的回调函数
   return Promise.reject(new Error('faile'))
}  
)
// 对外暴露
export default requests