<template>
     <div class="swiper-container" id="floor1Swiper">
                <div class="swiper-wrapper">
                  <div class="swiper-slide" v-for="(a) in list" :key="a.id">
                    <img :src="a.imgUrl" />
                  </div>
                </div>
                <!-- 如果需要分页器 -->
                <div class="swiper-pagination"></div>

                <!-- 如果需要导航按钮 -->
                <div class="swiper-button-prev"></div>
                <div class="swiper-button-next"></div>
              </div>
</template>

<script>
import Swiper from 'swiper'
export default {
name:'Carsousel',
props:['list'],
watch:{
  list:{
    // 立即监听：不管你数据有没有发生变化，我上来立即监听一次
    // 为什么watch监听不到list，因为数据从来没有发生变化（数据是父亲给的，父亲给的时候就是一个对象，对象里面该有的数据都是有的）

    immediate:true,
    handler(){
      this.$nextTick(()=>{
           // 第一次书写Swper的时候：在mounted当中书写是不可以的，但是为什么现在这里可以了？
  // 第一次书写轮播图的时候，是在当前组件内部发送请求、动态渲染结构【前台至少服务器数据需要回来】，因此当前的写法在这里不行
  // 现在的这种写法为什么可以：因为请求是父组件发的，父组件通过props传递过来的，而且结构都已经有了的情况下执行mounted
         var mySwiper = new Swiper ('.swiper-container', {
        //   direction: 'vertical', // 垂直切换选项
          loop: true, // 循环模式选项
          
          // 如果需要分页器
          pagination: {
            el: '.swiper-pagination',
          },
          
          // 如果需要前进后退按钮
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
          },
          
         
        })  
      })
    }
  }
}

}
</script>

