// 引入这个方法
import {reqGetSearchInfo} from '@/api'
// search模块的小仓库
const state={
    searchList:{}
}
//  修改state的唯一手段
const mutations={
    GETSEARCHLIST(state,searchList){
        state.searchList=searchList
    }
}
//action处理action,可以书写自己的业务逻辑，也可以处理异步 
const  actions={
    // 获取search模块数据
   async getSearchList({commit},params={}){
    //    至少传递一个参数（空对象）
      let result=  await reqGetSearchInfo(params)
      if(result.code==200){
          commit('GETSEARCHLIST',result.data)
      }
    }
}
// getters理解为计算书慈宁宫，用于简化仓库数据，让组件获取仓库数据更加方便
const getters={
    // 当前形参是当前仓库的state并非大仓库的state
    goodsList(state){
        // 这样书写是有问题的加||[]，如果服务器的数据回来了，没问题
        // 如果网络不给力或者没有网，返回的应该就是一个空数组，如果返回的是undefined,如果遍历了就会报错
        return state.searchList.goodsList||[];
    },
    trademarkList(state){
       // 这样书写是有问题的
       return state.searchList.trademarkList||[]; 
    },
    attrsList(state){
        // 这样书写是有问题的
        return state.searchList.attrsList||[]; 
     },
}
export default {
    state,
    mutations,
    actions,
    getters

}