import {reqGoodsInfo} from '@/api'
const state={
    goodinfo:{}
}
const actions={
// 获取产品信息的action
  async  getGoodInfo({commit},skuId){
      let result=  await reqGoodsInfo(skuId)
       if(result.code==200){
           commit('GETGETGOODINFO',result.data)
       }
    },
   

}
const mutations={
    GETGETGOODINFO(state,goodinfo){
        state.goodinfo=goodinfo
    }
}
const getters={}

export default {
    state,
    mutations,
    actions,
    getters

}