// 引入路由组件
import Home from '@/pages/Home'
import Search from '@/pages/Search'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
// 引入路由组件详情页
import Detail from '@/pages/Detail'



// 路由的配置信息
export default  [
    // 这里面的show是三级联动是否显示的判断
    {
        path:"/home",
        component:Home,
        meta:{show:true}
    },
    {
        name:'search',
        path:"/search/:keyword?",
        component:Search,
        meta:{show:true}
    },
    {
        path:"/login",
        component:Login,
        meta:{show:false}
    },
    {   
        path:"/register",
        component:Register,
        meta:{show:false}
    },
    {   
        name:'detail',
        path:'/detail/:skuid?',
        component:Detail,
        meta:{show:true}
    },
    // 重定向：当项目跑起来的时候，访问/立马就让他定向到首页
    {
        path:'*',
        redirect:'/home',
        meta:{show:true}
    }

]