<template>
  <!-- 商品分类导航 -->
  <div class="type-nav">
    <div class="container">
      <div @mouseleave="leaveindex" @mouseenter="enterShow">
        <h2 class="all">全部商品分类</h2>
        <transition name="animate__animated animate__bounce animate__faster"
          appear
          enter-active-class="animate__bounceInLeft"
          leave-active-class="animate__bounceOutLeft"
          >
        <div class="sort" v-show="show">
          <div class="all-sort-list2" @click="goSearch">
            <div
              class="item bo"
              v-for=" (c1,index) in categoryList"
              :key="c1.categorId"
              :class="{cur:currentIndex==index}"
                 >
              <h3 @mouseenter="changeIndex(index)">
                <a :data-categoryname='c1.categoryName' :data-category1Id='c1.categoryId'>{{c1.categoryName}}</a>
              </h3>
              <div class="item-list clearfix">
                <div class="subitem" v-for="c2 in c1.categoryChild" :key="c2.categoryId">
                  <dl class="fore">
                    <dt>
                      <a  :data-categoryname='c2.categoryName' :data-category2Id='c2.categoryId'>{{c2.categoryName}}</a>
                    </dt>
                    <dd>
                      <em v-for="c3 in c2.categoryChild" :key="c3.categoryId">
                        <a  :data-categoryname='c3.categoryName' :data-category3Id='c3.categoryId'>{{c3.categoryName}}</a>
                      </em>
                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>
        </transition>
        
      </div>

      <nav class="nav">
        <a href="###">服装城</a>
        <a href="###">美妆馆</a>
        <a href="###">尚品汇超市</a>
        <a href="###">全球购</a>
        <a href="###">闪购</a>
        <a href="###">团购</a>
        <a href="###">有趣</a>
        <a href="###">秒杀</a>
      </nav>
    </div>
  </div>
</template>

<script>
import 'animate.css';
import { mapState } from "vuex";
import throttle from 'lodash/throttle'
// console.log(_)
export default {
  name: "TypeNav",
  data() {
    return {
      currentIndex: -1,
      show:true
    };
  },
  // 组件挂载完毕，可以向服务器发送请求
  mounted() {
   
    // 当组件挂载完毕，让show的属性变为false
    if(this.$route.path!='/home'){
      this.show=false
    }
   
  },
  computed: {
    ...mapState({
      // 右侧需要的是一个函数，当使用这个计算属性的时候，右侧函数会立即执行一次
      // state即为大仓库中的数据
      categoryList: state => state.home.categoryList
    })
  },
  methods: {
    // changeIndex(index) {
    //   //   index鼠标以上某一个一级分类的元素的索引值
    //   this.currentIndex = index;
    // },
    changeIndex:throttle(function(index){
      this.currentIndex = index;
    },50),

    //   鼠标移出事件的回调
    leaveindex() {
      if(this.$route.path!='/home'){
        this.show=false
      }
      this.currentIndex = -1;
      
    },
    goSearch(event){
      // 把子节点当中的a标签，加上自定义属性data-categoryname，其余的子节点是没有的
      let elment=event.target
      // 获取当前触发这个事件的节点，需要找到带有data-categoryname属性的这个节点，说明点击的是a标签
      // 节点中有一个属性dataset属性，可以获取节点的自定义属性与属性值
       let {categoryname,category1id,category2id,category3id} =elment.dataset
      console.log(elment.dataset)
      if(categoryname){
        //  整理路由跳转的参数
        let location={name:'search'}
        let query={categoryName:categoryname}
        //  判断是几级分类的a标签
          if(category1id){
            query.category1Id=category1id

          }else if(category2id){
          query.category2Id=category2id
          }else{
            query.category3Id=category3id
          }
          //判断如果路由跳转的时候，带有params参数，捎带就传递过去
          if(this.$route.params){
            location.params=this.$route.params
          }
        // 整理完参数
        location.query=query
        // console.log(location,query)

        // 路由跳转
        this.$router.push(location)
       }
   
    },
    // 当鼠标移入的时候让商品分类显示
    enterShow(){
      this.show=true
    }
  }
};
</script>

<style lang='less' scoped>
.cur {
  background-color: rgb(162, 145, 190);
}
.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }

          &:hover {
            .item-list {
              display: block;
            }
          }
        }
      }
    }
  }
}
</style>