// 当前这个模块，api统一管理
import requests from './request'
// 引入mockrequest
import mockRequests from './mockaxios'
// 获取三级联动数据，三级联动的接口
// /api/product/getBaseCategoryList  get请求无参数
// 发请求axios返回的结果是promise对象
export const reqCategoryList=()=> requests({
    url:'/product/getBaseCategoryList',method:'get'})

 // 对外暴露,获取banner(home首页轮播的接口)
    export const reqGetBannerList=()=>mockRequests.get('/banner')

// 获取floor数据
export const reqFloorList=()=>mockRequests.get('/floor')


// 获取搜索模块的数据/api/list 请求的方式是post,需要带参数，前面的请求都是get请求，这个是post要带参数
// 当前的接口(搜索模块的数据)给服务器传递的参数至少是一个空对象
export const reqGetSearchInfo=(params)=>requests({
    url:'/list',
    method:'post',
    // 返回的是指定params的数据？？？？？？？？？？？？？
    data:params,
})

// 获取产品详情信息的接口/api/item/{ skuId }   GET
export const reqGoodsInfo=(skuId)=>requests({
    url:`/item/${skuId}`,
    method:'get',
    
})

